// * Make invisible scroll

@mixin reset-scroll {
	-ms-overflow-style: none;
	scrollbar-width: none;
	scrollbar-color: transparent transparent;

	&::-webkit-scrollbar {
        -webkit-appearance: none;
        display: none;
		width: 0;
		height: 0;
	}
	&::-webkit-scrollbar-track {
		background: transparent;
	}
	&:-webkit-scrollbar-thumb {
		background: transparent;
		border: none;
	}
}
